import React, { createContext, useContext, useMemo, useEffect } from 'react'
import { io } from 'socket.io-client'

export const SocketContext = createContext(null)

export const useSocket = () => {
    return useContext(SocketContext);
}

function SocketProvider(props) {
    
    // const optional_url = 'http://localhost:8080';
    const optional_url = 'https://chalk-eight-lime.glitch.me/';
    // Using environment variable for socket server URL
    const socketServerUrl = process.env.REACT_APP_SOCKET_SERVER_URL || optional_url;

    // Using useMemo to establish the socket connection once
    const socket = useMemo(() => io(socketServerUrl, {autoConnect: false}), [socketServerUrl]);

    // Using useEffect to disconnect on unmount
    useEffect(() => {
        return () => {
            socket.disconnect();
        };
    }, [socket]);

  return (
    <SocketContext.Provider value={socket}>
        {props.children}
    </SocketContext.Provider>
  )
}

export default SocketProvider